import React, { useState, useEffect } from "react";
import A from "../A";
import { getCategories } from "../../http/gig-calls";
import Button from "../Button";
import Slider from "@material-ui/core/Slider";
import { GetAssetImage } from "../../globalFunctions";
import "./styles.scss";
import { InputBase } from "@material-ui/core/InputBase";
import { makeStyles } from "@material-ui/core/styles";
import Accordion from "@material-ui/core/Accordion";
import AccordionSummary from "@material-ui/core/AccordionSummary";
import AccordionDetails from "@material-ui/core/AccordionDetails";
import Typography from "@material-ui/core/Typography";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import Checkbox from "@material-ui/core/Checkbox";
import FormControlLabel from "@material-ui/core/FormControlLabel";

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
    marginTop: "20px",
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    fontWeight: theme.typography.fontWeightRegular,
  },
}));

const GroupSocialFilter = (props) => {
  const [search, setSearch] = useState("");
  const [location, setLocation] = useState("");
  const [value, setValue] = React.useState([2, 10]);
  const [checked, setChecked] = React.useState(true);
  const [category, setcategory] = React.useState({
    All: true,
    Posts: true,
    People: true,
    Photos: true,
    Videos: true,
    Pages: true,
    Groups: true,
    Events: true,
  });
  const rangeSelector = (event, newValue) => {
    setValue(newValue);
    console.log(newValue);
  };

  const categories = [
    "All",
    "Posts",
    "People",
    "Photos",
    "Videos",
    "Pages",
    "Groups",
    "Events",
  ];

  const subscriptions = [
    "Free",
    "Paid",
    "Free & Paid",
    "Trial",
    "One Time Recurring",
  ];

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };
  const handleCheckboxChange = (event) => {
    setChecked(event.target.checked);
  };
  function valuetext(value) {
    return `${value}°C`;
  }
  const classes = useStyles();

  return (
    <div className="widget empty-inner-container-with-border filterWidget">
      <div className="container">
        <div className="row">
          <ul className="list-group w-100">
            <li className="widgetTitle">
              <img src={GetAssetImage("Icon feather-filter.png")} />
              <span>Filter</span>
            </li>
          </ul>
          <form className="form p-3 w-100">
            <div className="form-group">
              <input
                type="text"
                className="form-control"
                placeholder="Search Group"
                value={search}
                onChange={(e) => {
                  setSearch(e.target.value);
                }}
              />
              <div className={classes.root}>
                <Accordion>
                  <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panel1a-content"
                    id="panel1a-header"
                  >
                    <Typography className={classes.heading}>
                      Category
                    </Typography>
                  </AccordionSummary>
                  <AccordionDetails>
                    <Typography>
                      {categories.map((cate) => (
                        <div key={cate} className="form-group row">
                          <div className="col-sm-10">
                            <div className="form-check">
                              <FormControlLabel
                                control={
                                  <Checkbox
                                    checked={category.checkedB}
                                    onChange={handleCheckboxChange}
                                    name={cate}
                                    color="primary"
                                  />
                                }
                                label={cate}
                              />
                            </div>
                          </div>
                        </div>
                      ))}
                    </Typography>
                  </AccordionDetails>
                </Accordion>
                <Accordion>
                  <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panel2a-content"
                    id="panel2a-header"
                  >
                    <Typography className={classes.heading}>
                      Subscription
                    </Typography>
                  </AccordionSummary>
                  <AccordionDetails>
                    <Typography>
                      {subscriptions.map((cate) => (
                        <div key={cate} className="form-group row">
                          <div className="col-sm-10">
                            <div className="form-check">
                              <input
                                className="form-check-input"
                                type="checkbox"
                                id="gridCheck1"
                              />
                              <label
                                className="form-check-label"
                                for="gridCheck1"
                              >
                                {cate}
                              </label>
                            </div>
                          </div>
                        </div>
                      ))}
                    </Typography>
                  </AccordionDetails>
                </Accordion>
                <Accordion>
                  <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panel2a-content"
                    id="panel2a-header"
                  >
                    <Typography className={classes.heading}>
                      Price Range
                    </Typography>
                  </AccordionSummary>
                  <AccordionDetails>
                    <Slider
                      value={value}
                      onChange={handleChange}
                      valueLabelDisplay="auto"
                      aria-labelledby="range-slider"
                      getAriaValueText={valuetext}
                    />
                  </AccordionDetails>
                </Accordion>
                <Accordion>
                  <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panel2a-content"
                    id="panel2a-header"
                  >
                    <Typography className={classes.heading}>
                      Location
                    </Typography>
                  </AccordionSummary>
                  <AccordionDetails>
                    <input
                      type="text"
                      className="form-control"
                      placeholder="Search Location"
                      value={location}
                      onChange={(e) => {
                        setLocation(e.target.value);
                      }}
                    />
                  </AccordionDetails>
                </Accordion>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default GroupSocialFilter;
